/* Corrige contraste en inputs/textarea y alto mínimo del editor */
.field__input {
  color: #e9eef6;
  background: #0f141b;
}

.field__input::placeholder {
  color: transparent;
}

.field__textarea_2 {
  min-height: 40rem;
}

/* ====== SELECT: color visible + capa clickeable + label flotante ====== */
.field__select {
  appearance: none;
  background-image: linear-gradient(45deg, transparent 50%, var(--muted) 50%), linear-gradient(135deg, var(--muted) 50%, transparent 50%), linear-gradient(to right, transparent, transparent);
  background-position: calc(100% - 22px) calc(50% - 3px), calc(100% - 16px) calc(50% - 3px), 100% 0;
  background-size: 6px 6px, 6px 6px, 2.5rem 100%;
  background-repeat: no-repeat;
  color: #e9eef6; /* <<< importante */
  z-index: 1; /* sobre label */
}

.field__select option {
  color: #fff;
} /* lista desplegada, fondo claro del sistema */
.field select.field__input + .field__label {
  transform: translateY(-12px) scale(0.88);
  color: var(--muted);
  background: var(--card);
}

.field select.field__input:focus + .field__label {
  color: var(--primary);
}

/* ====== UPLOADER: no tapar otros elementos ====== */
.uploader {
  position: relative;
  overflow: hidden;
}

.uploader__input {
  position: absolute;
  inset: 0;
  opacity: 0;
  cursor: pointer;
  z-index: 2; /* por encima del label */
}

.uploader__label {
  position: relative;
  z-index: 1;
}

/* ====== MISC ====== */
.contenido-bloque {
  color: var(--text);
} /* asegura texto visible en paneles */
/* ambos paneles se comportan como “cards” con contenido en columna */
.detalle-falla,
.historial-falla {
  display: flex;
  flex-direction: column;
  /* opcional: asegura un mínimo agradable, ajusta a tu gusto */
  min-height: 340px;
