}

/* estructura: .historial-falla > .historial > .chat */
.historial-falla .historial {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 1; /* ocupa todo el alto del panel */
  min-height: 0; /* permite que su hijo pueda scrollear */
}

/* la lista de mensajes es la que scrollea */
.historial-falla .chat {
  flex: 1;
  min-height: 0; /* IMPORTANTE en flexbox para habilitar overflow */
  overflow: auto;
  padding-right: 8px; /* deja espacio para el scroll */
  scroll-behavior: smooth;
}

/* opcional: estiliza la barra para que combine con el tema */
.historial-falla .chat::-webkit-scrollbar {
  width: 10px;
}

.historial-falla .chat::-webkit-scrollbar-track {
  background: #0f141b;
  border-radius: 10px;
}

.historial-falla .chat::-webkit-scrollbar-thumb {
  background: #243042;
  border-radius: 10px;
  border: 2px solid #0f141b;
}

/* -----------------------------
   Paleta base (ajústala a tu branding)
--------------------------------*/
:root {
  --bg: #0f1115;
  --card: #151a22;
  --card-2: #171d26;
  --text: #e8eef6;
  --muted: #93a1b5;
  --border: #243042;
  --primary: #4cd964; /* verde acento por defecto */
  --primary-rgb: 76, 217, 100; /* para el focus ring */
}

/* Contenedor general del formulario */
.form-falla {
  width: 100%;
  max-width: 880px;
  margin-inline: auto;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: clamp(20px, 3vw, 32px);
  display: grid;
  gap: clamp(14px, 2vw, 20px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
  margin-bottom: 5rem;
}
.form-falla__actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 4px;
}

/* Cada bloque/sección del formulario que termine con -falla */
.contenido-bloque {
  background: linear-gradient(180deg, var(--card) 0%, var(--card-2) 100%);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: clamp(12px, 2vw, 18px);
  transition: border-color 0.25s ease, box-shadow 0.25s ease;
  /* Aplica a todos tus bloques que terminan con -falla */
}
.contenido-bloque:focus-within {
  border-color: rgba(var(--primary-rgb), 0.55);
  box-shadow: 0 0 0 6px rgba(var(--primary-rgb), 0.08) inset;
}
/* Campo con etiqueta flotante */
.field {
  position: relative;
  /* Estado flotante (cuando hay valor o focus) */
}
.field__input {
  width: 100%;
  color: var(--text);
  background: #10151c;
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 18px 16px 14px 16px;
  line-height: 1.3;
  outline: none;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;
  /* Textarea variante */
}
.field__input::placeholder {
  color: transparent;
}
.field__input:hover {
  border-color: #2a394e;
}
.field__input:focus-visible {
  border-color: rgba(var(--primary-rgb), 0.8);
  box-shadow: 0 0 0 4px rgba(var(--primary-rgb), 0.15);
}
.field__input.field__textarea {
  min-height: 120px;
  resize: vertical;
  padding-top: 22px;
}
.field__label {
  position: absolute;
  inset: 0 auto auto 12px;
  top: 4px;
  transform-origin: left top;
  pointer-events: none;
  color: var(--muted);
  background: transparent;
  padding: 0 6px;
  transition: transform 0.15s ease, color 0.15s ease, background 0.15s ease;
  border-radius: 6px;
}
.field__input:not(:placeholder-shown) + .field__label, .field__input:focus + .field__label {
  transform: translateY(-12px) scale(0.88);
  color: var(--primary);
  background: var(--card);
}

/* Subir archivo – zona drag & drop */
.uploader {
  position: relative;
  border: 1.5px dashed #2a394e;
  background: #10151c;
  border-radius: 14px;
  padding: 18px;
  display: grid;
  gap: 4px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}
.uploader:hover {
  border-color: #345074;
}
.uploader:focus-within {
  border-color: rgba(var(--primary-rgb), 0.8);
  box-shadow: 0 0 0 4px rgba(var(--primary-rgb), 0.15);
}
.uploader__input {
  position: absolute;
  inset: 0;
  opacity: 0;
  cursor: pointer;
}
.uploader__label {
  display: grid;
  gap: 2px;
  pointer-events: none; /* El click lo capta el input invisble */
}
.uploader__title {
  color: var(--text);
  font-weight: 600;
  font-size: 0.975rem;
}
.uploader__hint {
  color: var(--muted);
  font-size: 0.88rem;
}

/* Responsivo suave */
@media (min-width: 760px) {
  .form-falla {
    grid-template-columns: 1fr 1fr;
  }
  .descripcion-falla,
  .solucion-falla,
  .palabraclave-falla,
  .multimedia-falla {
    grid-column: 1/-1;
  }
}
/* Select con mismo look de .field__input */
.field__select {
  appearance: none;
  background-image: linear-gradient(45deg, transparent 50%, var(--muted) 50%), linear-gradient(135deg, var(--muted) 50%, transparent 50%), linear-gradient(to right, transparent, transparent);
  background-position: calc(100% - 22px) calc(50% - 3px), calc(100% - 16px) calc(50% - 3px), 100% 0;
  background-size: 6px 6px, 6px 6px, 2.5rem 100%;
  background-repeat: no-repeat;
}
.field__select:focus-visible {
  background-image: linear-gradient(45deg, transparent 50%, var(--primary) 50%), linear-gradient(135deg, var(--primary) 50%, transparent 50%), linear-gradient(to right, transparent, transparent);
}

/* Corrige label flotante con <select>: siempre considerado "con valor" */
.field select.field__input + .field__label {
  transform: translateY(-12px) scale(0.88);
  color: var(--muted);
  background: var(--card);
}

.field select.field__input:focus + .field__label {
  color: var(--primary);
}

/* Switch para checkbox */
.switch {
  display: flex;
  align-items: center;
  gap: 12px;
  /* Texto */
  /* Estado checked */
  /* Focus visible accesible */
}
.switch__input {
  position: absolute;
  opacity: 0;
}
.switch__label {
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  /* Track */
  /* Thumb */
}
.switch__label::before {
  content: "";
  width: 44px;
  height: 24px;
  border-radius: 999px;
  background: #10151c;
  border: 1px solid var(--border);
  box-shadow: inset 0 0 0 2px #0f141b;
  transition: background 0.2s ease, border-color 0.2s ease, box-shadow 0.2s ease;
  display: inline-block;
}
.switch__label::after {
  content: "";
  position: absolute;
  left: 3px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--text);
  transform: translateX(0);
  transition: transform 0.2s ease, background 0.2s ease;
}
.switch__title {
  color: var(--text);
  font-weight: 600;
  font-size: 0.95rem;
}
.switch__input:checked + .switch__label::before {
  background: rgba(var(--primary-rgb), 0.25);
  border-color: rgba(var(--primary-rgb), 0.85);
  box-shadow: 0 0 0 4px rgba(var(--primary-rgb), 0.12);
}
.switch__input:checked + .switch__label::after {
  transform: translateX(20px);
  background: var(--primary);
}
.switch__input:focus-visible + .switch__label::before {
  box-shadow: 0 0 0 4px rgba(var(--primary-rgb), 0.15);
  border-color: rgba(var(--primary-rgb), 0.8);
}

/* Responsive: dos columnas suaves como en el otro form */
@media (min-width: 760px) {
  .nombre-falla, .correo-falla, .rol-falla,
