  height: calc(150vh - 260px); /* ajusta si tu header es más alto */
  border: 0;
  border-radius: 12px;
  background: #0f141b; /* por si tarda en cargar */
  box-shadow: inset 0 0 0 1px var(--border);
}

.pdf-desc p {
  color: var(--text);
  line-height: 1.5;
}

/* Botón ghost ya lo definiste; por si no, un mínimo: */
.btn-ghost {
  padding: 8px 12px;
  border-radius: 10px;
  border: 1px solid var(--border);
  background: #0f141b;
  color: var(--text);
  text-decoration: none;
}

.btn-ghost:hover {
  background: #141b24;
  border-color: #2a394e;
}

/* ======= TOKENS (oscuro limpio) ======= */
:root {
  --bg: #0c1117;
  --surface: #0f141b;
  --surface-2: #121925;
  --text: #e8eef6;
  --muted: #9fb0c2;
  --border: #243042;
  --primary: #4cd964;
  --primary-rgb: 76,217,100;
  --danger: #ff6b6b;
  --shadow: 0 8px 28px rgba(0,0,0,.35);
}

/* ======= LAYOUT GENERAL ======= */
.resenas-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: clamp(12px, 2vw, 18px);
  color: var(--text);
}

/* ======= HEADER ======= */
.resenas-header {
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 14px 16px;
  box-shadow: var(--shadow);
  display: grid;
  gap: 10px;
}

.resenas-header .btn-ghost {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border-radius: 12px;
  border: 1px solid var(--border);
  color: var(--text);
  background: #0f141b;
  text-decoration: none;
}

.resenas-header .btn-ghost:hover {
  background: #141b24;
  border-color: #2a394e;
}

.ticket-title {
  margin: 0;
  font-weight: 900;
  letter-spacing: 0.2px;
  font-size: clamp(1.2rem, 2.4vw, 1.6rem);
  color: var(--text);
}

.libro-meta {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  align-items: center;
}

.libro-meta .chip {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 4px 10px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: #121925;
  color: var(--text);
  font-weight: 700;
  font-size: 0.82rem;
}

.libro-meta .btn-primary {
  margin-left: auto;
  padding: 10px 14px;
  border-radius: 12px;
  border: 0;
  background: linear-gradient(135deg, #b82b88, #ef9f40); /* tus colores $primary y $cta */
  color: var(--text);
  text-decoration: none;
}

.libro-meta .btn-primary:hover {
  filter: brightness(0.98);
}

/* ======= RESUMEN DE RATING ======= */
.rating-summary {
  margin-top: 12px;
  display: flex;
  align-items: center;
  gap: 16px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 12px 16px;
  box-shadow: var(--shadow);
}

.rating-summary__value {
  display: flex;
  align-items: baseline;
  gap: 8px;
}

.rating-big {
  font-size: 2rem;
  font-weight: 900;
  color: var(--text);
}

.rating-summary__meta {
  color: var(--muted);
  font-weight: 600;
}

/* ======= GRID PRINCIPAL ======= */
.resenas-grid {
  margin-top: 16px;
  display: grid;
  gap: 16px;
}

@media (min-width: 1024px) {
  .resenas-grid {
    grid-template-columns: 1fr 1fr;
    align-items: start;
  }
}
/* ======= FORMULARIO DE RESEÑA ======= */
.resena-form .section-title,
.resena-lista .section-title {
  margin: 0 0 10px 0;
  font-weight: 800;
  color: var(--text);
}

.resena-form .form-falla {
  display: grid;
  gap: 12px;
}

.resena-form .field {
  display: grid;
  gap: 6px;
}

/* Aplica a inputs/selects, NO a textarea */
.resena-form .field__input:not(textarea) {
  background: #0f141b;
  border: 1px solid var(--border);
  color: var(--text);
  border-radius: 12px;
  padding: 10px 12px; /* antes 10px 170px */
  width: 100%;
  box-sizing: border-box;
}

/* Estilo específico del textarea */
.resena-form .field__textarea {
  background: #0f141b;
  border: 1px solid var(--border);
  color: var(--text);
  border-radius: 12px;
  padding: 10px 12px; /* padding razonable */
  width: 380px;
  box-sizing: border-box;
  resize: none; /* si quieres bloquear el redimensionado */
  min-height: 120px;
  max-height: 200px;
  line-height: 1.5;
  writing-mode: horizontal-tb; /* explícito por si acaso */
  text-orientation: mixed;
  white-space: normal;
  overflow-wrap: break-word; /* evita cortes raros */
}

/* Estrellas accesibles y claras */
.stars {
  display: inline-flex;
  flex-direction: row-reverse;
  gap: 6px;
  border: 0;
  margin: 0;
  padding: 0;
}

.stars input {
  display: none;
}

.stars label {
  font-size: 1.8rem;
  line-height: 1;
  cursor: pointer;
  color: #4b5563;
  transition: transform 0.06s ease, color 0.15s ease;
}

.stars label:hover,
.stars label:hover ~ label {
  color: var(--primary);
}

.stars input:checked ~ label {
  color: var(--primary);
}

/* Acciones del formulario */
.form-falla__actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  align-items: center;
}

.form-falla__actions .btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 16px;
  min-height: 40px;
  border-radius: 12px;
  cursor: pointer;
  border: 0;
  text-decoration: none;
  font-weight: 700;
}

.form-falla__actions .btn-primary {
  background: linear-gradient(135deg, #b82b88, #ef9f40); /* tus colores $primary y $cta */
}

.form-falla__actions .btn-primary:hover {
  filter: brightness(0.98);
}

.form-falla__actions .btn-ghost {
  background: linear-gradient(135deg, #b82b88, #ef9f40); /* tus colores $primary y $cta */
}

.form-falla__actions .btn-ghost:hover {
  color: var(--text);
  border-color: #2a394e;
}

/* ======= LISTA DE RESEÑAS ======= */
.resenas {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 12px;
}

.resena {
  background: var(--surface-2);
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 12px;
}

.resena__header {
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  flex-wrap: wrap;
}

.resena .autor {
  font-weight: 800;
  color: var(--text);
}

.resena .fecha {
  color: var(--muted);
  font-size: 0.9rem;
}

.stars-inline {
  color: var(--primary);
  font-weight: 800;
}

.resena__text {
  margin: 0.4rem 0 0;
  line-height: 1.5;
}

/* ======= PAGINACIÓN ======= */
.paginacion {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin-top: 10px;
}

.paginacion .page {
  padding: 8px 12px;
  border-radius: 10px;
  text-decoration: none;
  background: #0f141b;
  border: 1px solid var(--border);
  color: var(--text);
}

.paginacion .page.is-active {
  background: rgba(var(--primary-rgb), 0.12);
  border-color: rgba(var(--primary-rgb), 0.6);
}

/* ======= UTILIDADES ======= */
.muted {
  color: var(--muted);
}