@use '../base/variables' as var;

html, body {
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: var.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    min-height: 100vh;
}
p {
    font-size: 1.8rem;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: var.$fuente_titulos;
    font-weight: var.$regular;
    font-style: normal;
    margin: calc(var.$separacion / 2) 0;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
    color: var.$secundario;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}

main {
    flex: 1; 
}